#contact {
    width: 100%;
    height: 100vh;

    background-color: rgb(24, 24, 24);

    display: flex;
    flex-direction: row;

    .contact-left {
        width: 33%;
        height: 100%;

        background-color: #8758FF;

        display: flex;
        flex-direction: column;
        align-items: flex-start;
        justify-content: center;

        h1 {
            font-size: 3.5rem;
            margin-left: 60px;
        }

        .contact-name {
            position: relative;
            top: 43.5%;
            margin-left: 10px;
        }
    }

    .contact-right {
        color: #F2F2F2;
        padding-left: 6rem;
        padding-top: 4rem;
        width: 66%;

        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;

        h2 {
            margin-bottom: 2rem;
        }

        .contact-icons {
            display: flex;
            flex-direction: row;

            .contact-icons-element > a {
                display: flex;
                flex-direction: column;
                align-items: center;

                margin: 0 40px;

                text-decoration: none;
                color: inherit;
            }

            svg {
                width: 30px;
                height: 30px;
            }
        }
        

        .contact-form {
            margin-top: 90px;
            display: flex;
            flex-direction: column;

            h2 {
                margin-bottom: 10px;
            }

            .form-input {
                width: 300px;
                height: 50px;
                margin-bottom: 5px;

                border: 0px solid black;
                border-radius: 8px;

                transition: all 0.3s ease-in-out;
                &:hover {
                    background-color: #bea7ff;
                }
            }

            input[type="text"], input[type="email"], textarea{
                font-family: 'DM Sans', sans-serif;
                font-weight: 700;
                padding: 10px;
            }

            button {
                border: 0px solid black;
                border-radius: 8px;

                height: 35px;
                
                font-family: 'DM Sans', sans-serif;
                font-weight: 700;

                transition: all 0.3s ease-in-out;
                &:hover {
                    background-color: #8758FF;
                }
            }

            #form-message{
                height: 80px;
                width: 500px;
            }
        }
    }
}