#about {
    width: 100%;
    height: 100vh;

    background-color: rgb(24, 24, 24);

    display: flex;
    flex-direction: row;

    .about-left {
        width: 33%;
        height: 100%;

        background-color: #8758FF;

        display: flex;
        flex-direction: column;
        align-items: flex-start;
        justify-content: center;

        h1 {
            font-size: 3.5rem;
            margin-left: 70px;
        }

        .about-socials {
            align-self: center;

            position: relative;
            top: 37%;
            width: 100%;
        }
    }

    .about-right {
        color: #F2F2F2;
        padding-left: 6rem;
        padding-top: 9rem;
        max-width: 66%;

        .about-story {
            margin-bottom: 30px;
        }

        .about-motivation {
            font-weight: 700;
        }

        .about-skills-title {
            margin: 60px 0;
        }

        .about-skills  {
            display: flex;
            flex-direction: row;
            justify-content: center;

            svg {
                width: 80px;
                height: 80px;
            }

            .about-skills-element {
                display: flex;
                flex-direction: column;
                align-items: center;

                margin-right: 70px;
                
                span {
                    margin-top: 10px;
                }
            }
        }
    }
}